<script>
export default {
    data() {
        return {
            isMenuOpen: false,
            isScrollDown: false,
            none: false,
            isHide: false,
            preScroll: "",
            activeMenu: false,
        };
    },
    methods: {
        onWaypoint({ going }) {
            if (going === this.$waypointMap.GOING_OUT) {
                this.isScrollDown = true;
            } else {
                this.isScrollDown = false;
            }
        },
        display_none() {
            this.none = true;
        },

        close_menu() {
            this.isMenuOpen = false;
            document.body.classList.remove("lock-scroll");
        },
        open_menu() {
            this.isMenuOpen = !this.isMenuOpen;
            document.body.classList.toggle("lock-scroll");
        },
        home_height() {
            var height = this.$refs.homeHeight.clientHeight;
            this.$emit("home_height", height);
        },
        inHome() {
            this.$emit("inHome");
        },
        topNav() {
            var height = this.$refs.homeHeight.clientHeight;
            var previousScroll = this.preScroll;
            var currentScrollPos = window.pageYOffset;
            if (currentScrollPos > previousScroll && currentScrollPos >= height * 0.5) {
                this.isHide = true;
            } else if (currentScrollPos < previousScroll) {
                this.isHide = false;
            }
            this.preScroll = currentScrollPos;
        },
        top_nav_scroll(){
            var currentScrollPos = window.pageYOffset;
            if (currentScrollPos > 1) {
                this.activeMenu = true
            }else{
                this.activeMenu = false

            }
        }
    },
    mounted() {
        this.preScroll = window.pageYOffset;
        window.addEventListener("scroll", this.home_height);
        window.addEventListener("scroll", this.topNav);
        window.addEventListener("scroll", this.top_nav_scroll);
    },
};
</script>

