    stroke-dashoffset: 255;
    animation: end-polygon 1s ease forwards;
}
.top_nav_logo:hover #Layer_1 polygon {
    animation: line-anim 1s forwards;
}
@keyframes line-anim {
    from {
        stroke-dashoffset: 255;
    }
    to {
        stroke-dashoffset: 0;
    }
}
@keyframes end-polygon {
    from {
        stroke-dashoffset: 510;
    }
    to {
        stroke-dashoffset: 255;
    }
}
@keyframes fill {
