<template>
    <div>
        <Home @home_height="getHeight" @inHome="markHome" />
        <!-- Navigation area Start -->
        <section id="navigation" :class="{ 'nav-wrap': isScroll, 'hide-nav': isHide }">
            <div class="container-fluid">
                <div class="d-flex  justify-content-around">
                    <div class="logo">
                        <a href="#body" class="logo-text">Rashid</a>
                    </div>
                    <div class="resu mid-nav">
                        <a href="">Resume</a>
                    </div>
                </div>
            </div>
        </section>
        <div class="right_nav" :class="{ 'right_menu-wrap': isScroll,'hide-right_menu': isHide }">
            <ul>
                <a href="#about"><li :class="{'right_nav-active':isAbout}"> <img src="images/menu_img/user.png" alt=""></li> </a>
                <a href="#work"><li :class="{'right_nav-active':isProject}"><img src="images/menu_img/portfolio.png" alt=""></li> </a>
                <a href="#skill"><li :class="{'right_nav-active':isSkill}"><img src="images/menu_img/pencil.png" alt=""></li> </a>
                <a href="#contact"><li :class="{'right_nav-active':isContact}" ><img src="images/menu_img/mail.png" alt=""></li> </a>
            </ul>
        </div>
        <!-- Navigation area End -->
        <About @inAbout="markAbout" />
        <Portfolio @inProject="markProject" />
        <!-- <Experience @inExperience="markExperience" /> -->
        <Skills @inSkill="markSkill" />
        <!-- <Testimonial @inTesti="markTesti" />  -->
        <Contact @inContact="markContact" />
    </div>
</template>

<script>
import Home from "./components/Home.vue";
// import Experience from "./components/Experience.vue";
import Portfolio from "./components/Portfolio.vue";
import Skills from "./components/Skills.vue";
// import Testimonial from "./components/Testimonial.vue";
import Contact from "./components/Contact.vue";
import About from "./components/About.vue";
export default {
    components: {
        About,
        Contact,
        // Testimonial,
        Skills,
        Portfolio,

        Home,
    },
    data() {
        return {
            isHide: false,
            preScroll: "",
            isClicked: false,
            isScroll: false,
            home_height: "",
            isHome: true,
            isAbout: false,
            isExperience: false,
            isProject: false,
            isSkill: false,
            isTestimonial: false,
            isContact: false,
        };
    },
    methods: {
        toggleMenu() {
            this.isClicked = !this.isClicked;
        },
        getHeight(value) {
            this.home_height = value;
        },
        ifScroll() {
            var scrollPosition = window.pageYOffset;
            if (scrollPosition > this.home_height * .6) {
                this.isScroll = true;
            } else {
                this.isScroll = false;
            }
        },
        mark_nothing() {
            this.isHome = false;
            this.isAbout = false;
            this.isExperience = false;
            this.isSkill = false;
            this.isProject = false;
            this.isTestimonial = false;
            this.isContact = false;
        },
        markHome() {
            this.mark_nothing();
            this.isHome = true;
        },
        markAbout() {
            this.mark_nothing();
            this.isAbout = true;
        },
        markExperience() {
            this.mark_nothing();
            this.isExperience = true;
        },
        markProject() {
            this.mark_nothing();
            this.isProject = true;
        },
        markSkill() {
            this.mark_nothing();
            this.isSkill = true;
        },
        markTesti() {
            this.mark_nothing();
            this.isTestimonial = true;
        },
        markContact() {
            this.mark_nothing();
            this.isContact = true;
        },
        midNav() {
            var previousScroll = this.preScroll;
            var currentScrollPos = window.pageYOffset;
            if (currentScrollPos > previousScroll && currentScrollPos >= this.home_height * .9) {
                this.isHide = true;
            } else if(currentScrollPos < previousScroll) {
                this.isHide = false;
            }
            this.preScroll = currentScrollPos;
        },
    },
    mounted() {
        this.preScroll = window.pageYOffset;
        window.addEventListener("scroll", this.ifScroll);
        window.addEventListener("scroll", this.midNav);
    },
};
</script>

<style scoped>
.hide-nav {
    top: -67px;
    box-shadow: none  !important;
}
.right_menu-wrap{
    visibility: visible;
    transform: translateX(0vw);
}
.hide-right_menu{
    right: -105px;
}
.right_nav-active{
    background-color: #ea0d00;
}
</style>
