<script>
import emailjs from "emailjs-com";
import Swal from "sweetalert2";
export default {
    data() {
        return {
            name: "",
            email: "",
            message: "",
        };
    },
    methods: {
        inContact() {
            this.$emit("inContact");
        },
        sendEmail(e) {
            if ((this.name == "") | (this.email == "") | (this.message == "")) {
                Swal.fire({
                    title: "Opps!",
                    text: "Please Fill Out The Form",
                    icon: "error",
                    confirmButtonText: "Return",
                    customClass: {
                        title: "swal_error_title",
                        confirmButton:"swal_error_btn"
                    },
                });
            } else {
                try {
                    emailjs.sendForm("service_t4p6so3", "template_3ejuhue", e.target, "user_mH5I1tZ9oqAVKEOyThXIm", {
                        name: this.name,
                        email: this.email,
                        message: this.message,
                    });
                } catch (error) {
                    console.log({ error });
                }
                Swal.fire({
                    title: "Success!",
                    text: "Message Sent Successfully !",
                    icon: "success",
                    showConfirmButton: false,
                    timer: 1000,
                    customClass: {
                        title: "swal_success_title",
                    },
                });
                // Reset form field
                this.name = "";
                this.email = "";
                this.message = "";
            }
        },
    },
};
</script>

